import * as API from './'

export default {
  // 登录 
  login: params => {
    return API.POST('/api/login', params, true)
  },
  // 获取手机验证码 
  PostSmsLoginCode: (data) => {
    return API.POST(`/api/sms/login/code`, data, true)
  },
  // 手机验证码登录 
  PostSmsLogin: (data) => {
    return API.POST(`/api/sms/login`, data, true)
  },
  // 微信公众号登陆 
  PostWechatLogin: (data) => {
    return API.POST(`/api/wechat/login`, data)
  },
}



