<template>
  <div
    id="login_scroll"
    v-if="wechatShow"
  >
    <!-- bigPic -->
    <div class="login_pic">
      <div class="login_logo">
        <img
          src="../../assets/login/login_logo.png"
          alt=""
        />
        <span>哎哟有房后台管理</span>
      </div>
    </div>
    <!-- bigPic -->
    <div class="login_input">
      <div class="title">
        <mt-navbar v-model="selected">
          <mt-tab-item id="1">
            <span :class="selected ==1?'selectedColor':''">账号登录</span>
          </mt-tab-item>
<!--          <mt-tab-item id="2">-->
<!--            <span :class="selected ==2?'selectedColor':''">手机号登录</span>-->
<!--          </mt-tab-item>-->
        </mt-navbar>
      </div>
      <div class="mb12">
        <div
          class="input_t"
          v-if="selected == 1"
        >
          <div>用户名</div>
        </div>
        <div
          class="input_t"
          v-else
        >
          <div>手机号</div>
        </div>
        <label>
          <input
            v-if="selected == 1"
            class="input_b"
            type="text"
            v-model="username"
            placeholder="请输入用户名"
          />
          <div
            v-else
            style="display: flex; justify-content: space-between"
          >
            <input
              class="input_b"
              type="number"
              oninput="if(value.length>11)value=value.slice(0,11)"
              v-model="mobile"
              placeholder="请输入手机号"
            />
            <span
              v-if="showCount"
              class="c_f60"
              @click="getCode"
            >获取验证码</span>
            <span
              v-else
              class="c_f60"
            >{{ count }}s</span>
          </div>
          <span class="border"></span>
        </label>
      </div>
      <div
        class="mb12"
        style="margin-top:32px"
      >
        <div class="input_t">{{ selected == 1 ? '密码' : '验证码' }}</div>
        <label for="">
          <input
            v-if="selected == 1"
            class="input_b"
            type="password"
            v-model="password"
            placeholder="请输入密码"
          />
          <input
            v-else
            class="input_b"
            v-model="smsCode"
            placeholder="请输入验证码"
          />
          <span class="border"></span>
        </label>
      </div>
      <van-button
        @click="handleButtonClick"
        round
        class="login_botton"
      >登录</van-button>
      <div
        v-if="img_box_show"
        class="aaa"
      >
        ©成都小住科技有限公司
        <br />
        <span>版本号：{{ version }}</span>
      </div>
    </div>
  </div>
</template>
<script>
import { Toast } from 'mint-ui';
import API from '@/api/api_login.js';
import { PostSmsLoginCode } from '@/api/api_business_login';
import cookie from '@/common/cookie.js';
import axios from 'axios';
export default {
    name: 'Login',
    data() {
        return {
            username: '',
            password: '',
            innerHeight: null,
            jsonObj: '',
            version: null,
            img_box_show: true,
            selected: '1',
            mobile: null,
            waitTime: null,
            showCount: true,
            count: '',
            timer: null,
            wechatShow: true
        };
    },
    watch: {
        selected(neww, oidd) {
            if (neww == 2) {
                this.mobile = '';
                this.smsCode = '';
            } else {
                if (localStorage.getItem('login_user')) {
                    this.username = JSON.parse(localStorage.getItem('login_user')).username;
                    this.password = JSON.parse(localStorage.getItem('login_user')).password;
                }
            }
        }
    },
    created() {
        this.$store.commit('roles_vuex', '');
        localStorage.removeItem('current_user');
        localStorage.removeItem('roles');
        localStorage.removeItem('c_hotelAll');
        localStorage.removeItem('c_hotelId');
        localStorage.removeItem('c_hotelType');
        var myurl = this.getUrlParam('code');
        if (myurl != null && myurl.toString().length > 1) {
            this.wechatShow = false;
            this.wechat(myurl);
        }
        axios.get(`/static/version.json`, { headers: { 'Cache-Control': 'no-cache' } }).then((res) => {
            this.version = res.data.version;
        });
        cookie.delCookie('currentID');
        if (localStorage.getItem('login_user')) {
            this.username = JSON.parse(localStorage.getItem('login_user')).username;
            this.password = JSON.parse(localStorage.getItem('login_user')).password;
        }
    },
    methods: {
        // 获取网址的参数
        getUrlParam(name) {
            var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)');
            var r = window.location.search.substr(1).match(reg);
            if (r != null) return unescape(r[2]);
            return null;
        },
        // 微信公众号登陆
        wechat(code) {
            this.$indicator.open('登录中...');
            API.PostWechatLogin({ code: code })
                .then((res) => {
                    this.$indicator.close();
                    if (res && res.status == 1) {
                        cookie.setCookie('access-1u2s3e4r', 'nrwhhhwjgsn');
                        Toast({
                            message: res.msg,
                            duration: 1000
                        });
                        if (res.user) {
                            localStorage.setItem('current_user', JSON.stringify(res.user));
                            localStorage.setItem('roles', JSON.stringify(res.user.roles));
                        } else {
                            localStorage.removeItem('roles');
                        }
                        setTimeout(() => {
                            this.$router.replace({ path: '/' });
                        }, 1000);
                    } else {
                        Toast(res.msg);
                        setTimeout(() => {
                            this.$router.replace({ path: '/login' });
                        }, 1000);
                        setTimeout(() => {
                            this.$router.go('/');
                        }, 2000);
                    }
                })
                .catch((e) => {
                    console.log(e);
                });
        },
        getCode() {
            // 已验证191,181手机号
            if (!this.mobile || (this.mobile && !/^1[3-9]\d{9}$/.test(this.mobile))) {
                Toast('请正确输入手机号');
                this.mobile = '';
                return;
            } else {
                PostSmsLoginCode({ mobile: this.mobile }).done((res) => {
                    Toast('发送成功，请注意查收');
                    this.setTime(); //开始倒计时
                });
            }
        },
        setTime() {
            const TIME_COUNT = 60;
            if (!this.timer) {
                this.count = TIME_COUNT;
                this.showCount = false;
                this.timer = setInterval(() => {
                    if (this.count > 0 && this.count <= TIME_COUNT) {
                        this.count--;
                    } else {
                        this.showCount = true;
                        clearInterval(this.timer);
                        this.timer = null;
                    }
                }, 1000);
            }
        },
        //加密
        getAesPassword(password) {
          const key = '6169796f796f7566616e673132333435';
          // eslint-disable-next-line no-undef
            return CryptoJS.AES.encrypt(password, CryptoJS.enc.Hex.parse(key), {
              // eslint-disable-next-line no-undef
                iv: CryptoJS.enc.Hex.parse('30313233343536373839414243444546'),
              // eslint-disable-next-line no-undef
                mode: CryptoJS.mode.CBC,
              // eslint-disable-next-line no-undef
                padding: CryptoJS.pad.Pkcs7
            }).toString();
        },
        handleButtonClick() {
            if (this.selected == 1) {
                if (!this.username) {
                    Toast('用户名不能为空');
                } else if (!this.password) {
                    Toast('密码不能为空');
                } else {
                    this.getparams = {
                        username: this.username,
                        password: this.password
                    };
                    let password = this.getAesPassword(this.password);
                    const params = {
                        username: this.username,
                        password: password,
                        validateCode: 'aiyoyoufang'
                    };
                    API.login(params)
                        .then((res) => {
                            if (res && res.status == 1) {
                                var loginInfo = JSON.stringify(this.getparams);
                                localStorage.setItem('login_user', loginInfo);
                                var json = localStorage.getItem('login_user');
                                this.jsonObj = JSON.parse(json);
                                (this.username = this.jsonObj.username), (this.password = this.jsonObj.password);
                                cookie.setCookie('access-1u2s3e4r', 'nrwhhhwjgsn');
                                Toast({
                                    message: res.msg,
                                    duration: 1000
                                });
                                if (res.user) {
                                    localStorage.setItem('current_user', JSON.stringify(res.user));
                                    localStorage.setItem('roles', JSON.stringify(res.user.roles));
                                } else {
                                    localStorage.removeItem('roles');
                                }
                                setTimeout(() => {
                                    this.$router.push({ path: '/' });
                                }, 1000);
                            } else {
                                Toast(res.msg);
                                this.username = JSON.parse(localStorage.getItem('login_user')).username;
                                this.password = JSON.parse(localStorage.getItem('login_user')).password;
                            }
                        })
                        .catch((e) => {
                            console.log(e);
                        });
                }
            } else {
                if (!this.smsCode) {
                    Toast('请正确输入验证码');
                    return;
                }
                let params = {
                    mobile: this.mobile,
                    smsCode: this.smsCode
                };
                // 验证码登录
                API.PostSmsLogin(params)
                    .then((res) => {
                        if (res && res.status == 1) {
                            cookie.setCookie('access-1u2s3e4r', 'nrwhhhwjgsn');
                            Toast({
                                message: res.msg,
                                duration: 1000
                            });
                            if (res.user) {
                                localStorage.setItem('current_user', JSON.stringify(res.user));
                                localStorage.setItem('roles', JSON.stringify(res.user.roles));
                            } else {
                                localStorage.removeItem('roles');
                            }
                            setTimeout(() => {
                                this.$router.push({ path: '/' });
                            }, 1000);
                        } else {
                            Toast(res.msg);
                        }
                    })
                    .catch((e) => {
                        console.log(e);
                    });
            }
        }
    }
};
</script>
<style scoped>
body,
html {
    height: 100%;
}
#login_scroll {
    font-size: 14px;
}
.login_pic {
    background-image: url('../../assets/login/bigimage_bg.png');
    background-size: 100% 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 260px;
}
.login_logo {
    margin: auto;
    display: flex;
    margin: 60px 30px;
    align-items: center;
    color: #fff;
    font-size: 18px;
}
.login_logo > img {
    width: 60px;
    height: 60px;
    padding-right: 8px;
}
.login_input {
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
    position: relative;
    top: 200px;
    bottom: 0;
    left: 0;
    right: 0;
    background: #fff;
    overflow-y: scroll;
    text-align: left;
}
.title {
    padding: 20px 56px;
    color: #000;
    padding-bottom: 20px;
    display: flex;
    font-weight: bold;
}
.input_t {
    color: #888;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
}
.input_b {
    color: #000;
    width: 70%;
}
.border {
    display: inline-block;
    width: 100%;
    height: 1px;
    background: #b2b2b2;
    opacity: 0.24;
}
.mb12 {
    padding: 0 56px;
}
.login_botton {
    background: linear-gradient(135deg, #ff6119 0%, #ff7e45 100%);
    width: 70%;
    color: #fff;
    text-align: center;
    display: flex;
    margin: 32px auto;
    letter-spacing: 10px;
    justify-content: center;
}
.aaa {
    background-image: url('../../assets/login/image_bg.png');
    background-size: 100% 100%;
    padding: 24px;
    text-align: center;
    font-size: 14px;
    color: #888;
    line-height: 26px;
}
.bca {
    padding: 24px;
    font-size: 12px;
    color: #888;
    text-align: center;
    background-image: url('../../assets/login/image_bg.png');
    background-size: 100% 100%;
    opacity: 0.7;
    line-height: 26px;
    color: green;
}
.c_f60 {
    color: #ff6119;
}
.selectedColor {
    border-bottom: 3px solid #ff6119;
    padding-bottom: 20px;
}
::v-deep .mint-navbar {
    background-color: #fff;
    display: flex;
    width: 60%;
    text-align: left;
}
/deep/ .mint-tab-item-label {
    font-size: 14px;
}
input::-webkit-input-placeholder {
    color: #b2b2b2;
}
input::-moz-placeholder {
    color: #b2b2b2;
}
input:-moz-placeholder {
    color: #b2b2b2;
}
input:-ms-input-placeholder {
    color: #b2b2b2;
}
/deep/ .mint-navbar .mint-tab-item.is-selected {
    color: #ff6119;
    border-bottom: none;
}
</style>



